@font-face {
  font-family: 'Aileron';
  src: url('./Fonts/Aileron-Thin-webfont.eot');
  src: url('./Fonts/Aileron-Thin-webfont.eot?#iefix') format('embedded-opentype'),
       url('./Fonts/Aileron-Thin-webfont.woff') format('woff'),
       url('./Fonts/Aileron-Thin-webfont.ttf') format('truetype'),
       url('./Fonts/Aileron-Thin-webfont.svg#aileronthin') format('svg');
  font-weight: normal;
  font-style: normal;
}


.navbar{
  padding-left: 80px;
  padding-top: 0;
  padding-bottom: 0;
  background-color: #00798a;
  font-family: Aileron;
  vertical-align: text-top;
  height: 60px;
  width: 100%;
  vertical-align: middle;
  
}

.navbar-brand{
  height: 60px!important;
  padding-top: 0!important;
  padding-bottom: 0;
  margin-bottom: 0;
  margin-top: 0;
  background-color: #00798a;

}


.mainLinkP1{
  color: white;
  margin-bottom: 0px;
  margin-top:0px;
  padding-top: 5px;
  padding-bottom: 0px;
  font-weight: bold;
  font-size: 25px;
  line-height: 1;
}

.mainLinkP2{
  color: white;
  margin-bottom: 0px;
  margin-top:0px;
  padding-top: 0px!important;
  padding-bottom: 0px;
  vertical-align: top;
  line-height: 1.5;
}

.navbar-collapse{
  justify-content: space-around;
}

.navbar-nav{
  width: 100%;
  justify-content: space-between;
  padding-left: 80px;
  padding-right: 80px;
}

.header-toggler{
  vertical-align: middle;
}

.li{
  color:white !important;
  font-size: 21px;
  font-weight: normal;
  align-items: center;
  text-align: center;
  margin-top:0;
  padding-left: 15px;
  padding-right: 15px;
  background-color: #00798a;

}

.nav-link{
  background-color: #00798a;
  padding-left: 0;
}

.li a{
  color:white !important;
  font-size: 21px;
  font-weight: normal;
  align-items: center;
  margin-top:0;
  background-color: #00798a;
  /* background-color: #015d69; */
}

.dropdown-menu{
  padding-top:0;
  padding-bottom: 0;
  height: 28px;
  /* border-top-color: #015d69;
  border-bottom-color: #015d69; */
  border-top-color: #00798a;
  border-bottom-color: #00798a;

}

.dropdown-item{
  background-color: #00798a;
  height: 100%;
  vertical-align: center;
  padding-bottom: 0;
  padding-top:0;
}

.dropdown-item a {
  color: white !important;
  text-decoration: none;
  font-size: 17px;
  text-align: left;
  padding-left: 10px;
  height:100%;
  vertical-align: top;
}

.dropdown-item.active:active {
  background-color:#00798a !important;

}

.dropdown-item a:hover {
  font-weight: bold;
  text-decoration: none;
}

.dropdown-item:hover{
  background-color: #00798a;

}

.li:hover{
  font-weight: bold;
  text-decoration: none;
}

.mainLinkP1:hover{
  font-size: 28px;
  text-decoration: none;
}

.mainLinkP2:hover{
  font-weight: bold;
  text-decoration: none;
}

.nav-header a{
  text-align: left;
}

.nav-header a:hover{
  font-weight: bold;
  text-decoration: none;
}

.li_dropDown:hover{
  font-weight: bold;
  text-decoration: none;
}

@media(max-width: 1300px) {
  .navbar-nav{
    padding-left: 43px;
    padding-right: 43px;
  }
  .navbar{
    padding-left: 60px;
  }
}

@media(max-width: 1200px) {
  .navbar-nav{
    padding-left: 14px;
    padding-right: 14px;
  }
  .navbar{
    padding-right: 0px;
  }
}

@media(max-width: 1100px) {
  .navbar-nav{
    padding-left: 0px;
    padding-right: 0px;
  }
  .navbar{
    padding-right: 0px;
  }
  .li{
    /* padding-left: 5px;
    padding-right: 5px; */
      padding-left: 0px;
      padding-right: 0px;

  }
}

@media(max-width:991px){
  .navbar{
    padding-left: 40px;
    /* width: 130%; */
  }
  .navbar-nav{
    width: 400px;
    margin-left: -40px;
    /* padding-left: 60px; */
    background-color: #00798a;
    /* opacity: 0.8; */
    opacity: 1;
  }
  .li{
    font-size: 18px;
    font-weight: bold;
  }
  .nav-link{
    font-size: 18px;
  }
  .li a{
    font-weight: bold;
    font-size: 18px;
  }
  .dropdown-item a{
    font-weight: bold;
    font-size: 16px;
    width: 390px;
  }
  /* .navbar-toggler{
    margin-right: 1000px;
  } */
}
@media(max-width:750px){
  .navbar{
    padding-left: 20px;
  }
  .navbar-nav{
    margin-left: -20px;
    /* padding-left: 40px; */
    background-color: #00798a;

  }
  .navbar-nav{
    width: 300px;
  }
}
@media(max-width:600px){
  .navbar-nav{
    width: 200px;
    /* padding-left: 30px; */
    background-color: #00798a;
  }
  .dropdown-item a{
    width: 190px;
  }
}
@media(max-width:500px){
  .navbar-nav{
    width: 220px;
    /* padding-left: 20px; */
    background-color: #00798a;

  }
  .mainLinkP1{
    font-size: 22px;
  }
  .mainLinkP2{
    font-size: 22px;
  }
  .navbar-toggler{
    margin-right: 10px;
  }
}


@media(max-width:400px){
  .navbar{
    /* padding-left: 10; */
  }
}

@media(max-width:300px){
.mainLinkP1{
  font-size: 20px;
}
.mainLinkP2{
  font-size: 20px;
}
}

